.header{
	&__top{
		text-transform: uppercase;
		font-size: 11px;
		color: @white;
		background: #000;
		.fa-caret-down{
			margin-left: 10px;
			color: #c0c0c0;
		}
		.fa-user{
			margin-right: 10px;
			color: #666666;
		}
	}
	&__right-box,
	&__custom{
		height: 30px;
		line-height: 30px;
		background: #000;
	}
	&__right-box{
		text-align: right;
	}
	&__right-box-item{
		.inline-block;
		text-align: center;
	}
	&__us{
		width: 70px;
		border-right: 1px solid #464646;
		border-left: 1px solid #464646;
		.dropdown{
			.dropdown-icon(#c0c0c0, -30%);
		}
	}
	&__account{
		width: 125px;
	}
	&__search{
		padding: 0 8px;
		width: 30px;
		border-right: 1px solid #464646;
		border-left: 1px solid #464646;
	}
	.show-search{
		display: none;
	}
}
.header__bottom{
	padding: 20px 0;
	.logo{
		margin-top: 20px;
		width: 120px;
		height: 30px;
		.big{
			display: none;
		}
	}
	.show-main-menu{
		position: relative;
		padding: 0 18px;
		display: none;
		width: 100%;
		height: 48px;
		line-height: 48px;
		text-align: left;
		color: #fff;
		background: @maroon;
		cursor: pointer;
		z-index: 3;
		&__text{
			font-size: 18px;
			text-transform: uppercase;
		}
		&__icon{
			float: right;
			font-size: 22px;
		}
	}
	.main-menu{
		margin-top: 20px;
		max-width: 100%;
		text-align: right;
		cursor: pointer;
		&__item{
			.inline-block;
			&:last-child{
				.header__bottom .main-menu__link{
					padding-right: 20px;
				}
			}
		}
		&__link{
			display: block;
			padding:0 12px;
			padding-top: 4px;
			.arial;
			font-size: 16px;
			text-transform: lowercase;
			color: #000;
			.dropdown{
				.dropdown-icon(black, -20%);
			}
		}
	}
	.card{
		float: right;
		font-size: 12px;
		color: #929292;
		width: 210px;
		border: 1px solid #d5d5d5;
		&__list{
			width: 210px;
			height: 30px;
			line-height: 30px;
			border: none;
			border-bottom: 1px solid #d5d5d5;
			padding: 0 8px;
			option{
				width: 210px;
			}
			&:focus{
				outline: none;
			}
		}
		&__pay{
			height: 30px;
			line-height: 30px;
			text-align: right;
			padding: 0 8px;
			font-size: 11px;
			text-transform: uppercase;
			&-icon{
				float: left;
				position: relative;
			}
		}
		.fa-shopping-bag{
			font-size: 18px;
			position: absolute;
			top: 7px;
			left: 0;
		}
	}
}


@media screen and (max-width: 1200px) { 
	
}
@media screen and (max-width: 992px) { 
	.main-menu__container{
		position: relative;
		margin-top: 20px;
	}
	.header__bottom{
		.show-main-menu{
			display: block;
		}
		.main-menu{
			position: relative;
			display: none;
			margin-top: -5px;
			padding: 10px;
			width: 100%;
			height: auto;
			background: darken(@maroon, 10%);
			z-index: 2;
			&__item{
				display: block;
				margin-bottom: 10px;
				width: 100%;
				text-align: left;
			}
			&__link{
				padding: 10px;
				font-size: 30px;
				color: #fff;
				transition: all .5s;
				&:hover{
					background: @maroon;
				}
				.dropdown::after{
					border-color: transparent;
				}
			}
		}
	}
}
@media screen and (max-width: 768px) { 
	
}
@media screen and (max-width: 700px) { 
	.header{
		background: none;
		&__custom,
		&__right-box{
			padding: 0;
			width: 100%;
			height: auto;
			font-size: 30px;
			line-height: 95px;
		}
		&__custom{
			text-align: center;
			color: @black;
			background: @white;
		}
		&__us{
			border-left: 0;
			width: 24%;

		}
		&__account{
			width: 60%;
		}
		&__search{
			width: 13%;
			border-right: none;
		}
	}	
	.header__bottom{
		.logo-container{
			margin: 60px 0;
			width: 100%;
		}
		.logo{
			margin: 0 auto;
			width: 360px;
			height: 90px;
			.small{
				display: none;
			}
			.big{
				display: block;
			}
		}
		.card-container{
			width: 100%;
		}
		.card{
		float: none;
		font-size: 30px;
		width: 100%;
		border: 1px solid #d5d5d5;
		&__list{
			display: none;
		}
		&__pay{
			height: 96px;
			line-height: 96px;
			text-align: right;
			padding: 0 30px;
			font-size: 30px;
			text-transform: uppercase;
			&-icon{
				float: left;
				position: relative;
			}
			.fa-shopping-bag{
			font-size: 48px;
			position: absolute;
			top: 30px;
			left: 0;
		}
		}
      }
		.show-main-menu{
			padding: 0 30px;
			display: block;
			width: 100%;
			height: 96px;
			line-height: 96px;
			&__text{
				font-size: 30px;
			}
			&__icon{
				float: right;
				font-size: 36px;
			}
		}
	}
}
@media screen and (max-width: 615px) { 
	.header{
			&__right-box-item{
			background: @black;
			}
			&__us{
				border-left: none;
				width: 100%;
			}
			&__account{
				text-align: center;
				width: 100%;
			}
			&__search{
				width: 100%;
			}
			.show-search{
				display: block;
			}
			.fa-search{
				display: none;
			}
		}	
	}
@media screen and (max-width: 480px) { 
		.header{
			&__custom{
				height: auto;
			}
			.custom__item{
				.inline-block;
				width: 100%;
			}
		}
}