.slider-container{
	@media screen and (max-width: @width-tablet-700){
		display: none;
	}
	width: 100%;
	background: linear-gradient(to right, #f5f3ec, #ebe6de);
	.slick-dots{
	bottom: -42px;
	z-index: 1000;
		li {
			margin: 0 10px;
			width: 10px;
			height: 10px;
				button{
				width: 10px;
				height: 10px;
				background: #827976;
				border-radius: 50%;
			}
		}	
	}
	.slick-dots{
		li{
			color: transparent;
			border: none;
			outline: none;
		}
		.slick-active{
			button{
    			background: @maroon;
    			color: transparent;
			}
		}
	}
}
.slider{
	.container{
		position: relative;
	}
	&__item{
		height: 250px;
		background: none;
		&:focus{
			outline: none;
		}
		@media screen and (min-width: @width-min-tablet){
			height: 270px;
		}
		@media screen and (min-width: @width-tablet){
			height: 440px;
		}
		.col-xs-6{
			height: 100%;
		}
	}
	&__text{
		position: absolute;
		top: 50%;
		left: 50%;
		.times;
		text-align: center;
		transform: translateX(-50%) translateY(-50%);
	}
	&__slogan{
		.inline-block;
		width: 75px;
		font-size: 27px;
		font-style: italic;
	}
	&__big-title{
		font-size: 71px;
		font-weight: bold;
		line-height: .8;
		color: #c7876c;
	}
	&__small-title{
		font-size: 27px;
		color: #72645e;
	}
	&__img{
		height: 100%;
		img{
			height: 100%;
		}
	}
}
.slick-bottom{
		position: relative;
		height: 75px;
		background: #fff;
		&::after{
			content: '';
			position: absolute;
			top: -7px;
			left: 0;
			display: block;
			width: 100%;
			height: 7px;
			background: rgba(0,0,0,.4);
		}
}

