.gallery{
	@media screen and (max-width: 600px) {
		background: @maroon;
	}
	.show-tabs{
		display: none;
		padding: 0 20px;
		font-size: 15px;
		.line-height(47px);
		text-align: left;
		text-transform: uppercase;
		color: #fff;
		background: darken(@maroon, 10%);
		cursor: pointer;
		@media screen and (max-width: 600px) {
			display: block;
		}
	}
	.tabs{
		&__nav{
			@media  screen and (max-width: 600px) {
				display: none;
			}
			&-item{
				margin-left: -4px;
				padding: 0 20px;
				.inline-block;
				font-size: 15px;
				.line-height(47px);
				text-transform: uppercase;
				color: @tabColor;
				background: #fff;
				cursor: pointer;
				transition: all .5s;
				@media  screen and (max-width: 600px) {
					display: block;
					margin-left: 0;
				}
				&.active,
				&:hover{
					color: #fff;
					background: @maroon;
				}
			}
		}
		&__content{
			padding: 30px 0;
			background: @maroon;
			transition: all .5s;
			.row{
				display: none;
			}
			.row.active{
				display: block;
			}
			&-item{
				min-height: 300px;
				overflow: hidden;
				@media screen and (max-width: 992px) {
					height: 315px;
				}
				&:hover{
					box-shadow: 0 0 28px rgba(0,0,0,.35);
				}
			}
			.col-xs-4{
				@media screen and (max-width: 580px) {
					width: 50%;
				}
			}
			.col-xs-4.close{
				@media screen and (max-width: 580px) {
					display: none;
				}
			}
		}
		&__img{
			width: 100%;
			height: 290px;
			overflow: hidden;
			@media screen and (max-width: 992px) {
				height: 207px;
			}
			img{
				width: 100%;
				height: auto;
			}
		}
		&__price{
			padding: 10px 0;
			text-align: center;
			text-transform: uppercase;
			background: #fff;
			&-title{
				font-size: 12px;
			}
			&-content{
				font-size: 22px;
			}
			&-button{
				color: #fff;
			}
			.btn{
				padding: 0;
				padding-left: 10px;
				width: 120px;
				text-align: left;
			}
		}
	}
	.tabs__nav-item:first-child{
		margin-left: 0;
	}
}
.promo-icon{
	position: relative;
	padding: 24px 0;
		&::before,
		&::after{
			content: '';
			position: absolute;
			top: 50%;
			width: 40%;
			height: 1px;
			background: #000;
			@media  screen and (max-width: 600px) {
				width: 36%;
			}
		}
		&::before{
			left: 0;
		}
		&::after{
			right: 0;
		}
	&__img{
		margin: 0 auto;
		width: 157px;
		height: 157px;
		overflow: hidden;
		@media screen and (max-width: 992px) {
			width: 120px;
			height: 120px;
		}
		@media screen and (max-width: 600px) {
			width: 100px;
			height: 100px;
		}
		@media screen and (max-width: 480px) {
			width: 80px;
			height: 80px;
		}
	}
}
