* {
    padding: 0;
    margin: 0;
}

html,
body {
    height: 100%;
}

body {
    min-width: 320px;
    font-family: Arial, sans-serif;
    font-weight: normal;
    background: @white;
}

a:visited,
a:link,
a:hover {
    text-decoration: none;
}
.clearfix::after{
	content: '';
	display: block;
	clear: both;
}
.block{
	width: 100%;
	height: 100px;
	background: blue;
}
img{
	max-width: 100%;
	height: auto;
}
ul{
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.block{
	background: gray;
	margin-bottom: 20px;
}

