/* media
================================ */
@width-large: 1200px;
@width-desktop: 992px;
@width-tablet: 768px;
@width-tablet-700: 700px;
@width-min-tablet: 640px;
@width-phone: 480px;

/* fontst
==================================== */
.arial{
	font-family: Arial, sans-serif;
}
.times{
	font-family: Times New Roman, serif;
}
/* color
======================================== */
@white: #fff;
@black: #000;
@textGray: #9e9e9e;
@textDarkGray: #6c6c6c;
@iconColor: #6b6b6b;
@tabColor: #555;


/* bg
=================================== */
@maroon: #e4916e;
@darkMaroon: darken(@maroon, 10%);
@darkGray: #ececec;
@lightGray: #f7f7f7;
@promoBg: #d8d8d8;

