/* favorite
===========================================*/
.favorite__title {
  position: relative;
  margin-bottom: 20px;
  text-align: center;
}
.favorite__title::before,
.favorite__title::after {
  content: '';
  position: absolute;
  top: 50%;
  width: 37%;
  height: 1px;
  background: black;
}
@media screen and (max-width: 992px) {
  .favorite__title::before,
  .favorite__title::after {
    width: 34%;
  }
}
@media screen and (max-width: 700px) {
  .favorite__title::before,
  .favorite__title::after {
    width: 30%;
  }
}
@media screen and (max-width: 992px) {
  .favorite__title {
    display: none;
  }
}
.favorite__title::before {
  left: 0;
}
.favorite__title::after {
  right: 0;
}
.favorite__content {
  padding-bottom: 20px;
  border-bottom: 1px solid black;
}
@media screen and (max-width: 768px) {
  .favorite__content {
    display: none;
  }
}
.favorite__item {
  display: inline-block;
  vertical-align: middle;
  width: 100px;
  margin-right: 48px;
}
.favorite__item:last-child {
  margin-right: 0;
}
@media screen and (max-width: 1200px) {
  .favorite__item {
    margin-right: 15px;
  }
}
@media screen and (max-width: 992px) {
  .favorite__item.close {
    display: none;
  }
}
