.update{
	background: #fff;
	@media screen and (min-width: @width-tablet) {
		.line-height(55px);
		background: @darkGray;
	}
	&__label{
		@media screen  and (max-width: @width-tablet){
			display: none;
		}
	}
	&__form{
		margin-bottom: 25px;
	}
	&__form-group{
		position: relative;
		height: 96px;
		@media screen  and (min-width: @width-tablet){
			width: 255px;
			height: 55px;
		}
	}
	&__input{
		width: 100%;
		height: 100%;
		padding: 0 30px;
		padding-right: 126px;
		font-size: 30px;
		color: @textGray;
		background: @darkGray;
		box-sizing: border-box;
		border: none;
		@media screen  and (min-width: @width-tablet){
			.inline-block;
			margin-left: 5px;
			padding:0 10px;
			width: 135px;
			height: 30px;
			font-size: 12px;
			color: @textGray;
			background: #fff;
		}
	}
	.form-icon{
		position: absolute;
		top: 0;
		right: 0;
		width: 96px;
		height: 96px;
		background: @maroon;
		border-bottom: 7px solid @darkMaroon;
		@media screen  and (min-width: @width-tablet){
			top: 13px;
			width: 30px;
			height: 30px;
			border-bottom-width: 3px;
		}
		&__cross{
			position: absolute;
			top: 50%;
			left: 50%;
			.inline-block;
			background: #fff;
			transform: translate(-50%, -50%);
		}
		&__horizontal{
			width: 37px;
			height: 11px;
			@media screen  and (min-width: @width-tablet){
				width: 11px;
				height: 3px;
			}
		}
		&__vertical{
			width: 11px;
			height: 37px;
			@media screen  and (min-width: @width-tablet){
				width: 3px;
				height: 11px;
			}
		}
	}
		/* socials 
	================================*/
	&__socials{
		.line-height(88px);
		margin-bottom: 25px;
		padding: 0 83px;
		font-size: 27px;
		background: #fff;
		@media screen  and (min-width: @width-tablet){
				.line-height(55px);
				text-align: right;
				padding: 0;
				background: none;
			}
	}
	&__socials-text{
		color: @textDarkGray;
		@media screen  and (min-width: @width-tablet){
			margin-right: 15px;
			font-size: 16px;
		}
	}
	&__socials-icons{
		float: right;
		.fa{
			color: @iconColor;
		}
	}
	&__socials-item{
		.inline-block;
		margin-right: -8px;
		width: 88px;
		text-align: center;
		background: @lightGray;
		cursor: pointer;
		@media screen  and (min-width: @width-tablet){
				width: 55px;
			}
		&:nth-child(2){
			background: @darkGray;
		}
	}
	&__socials-block{
		.inline-block;
	}
}
