.inline-block(@valign: middle) {
    display: inline-block;
    vertical-align: @valign;
}

.line-height(@var){
	height: @var;
	line-height: @var;
}


.btn{
	.inline-block;
	padding: 0 20px;
	font-size: 11px;
	text-transform: uppercase;
	.line-height(40px);
	color: #fff;
	background: @maroon;
	border-bottom: 3px solid darken(@maroon, 10%);
	cursor: pointer;
	transition: background-color .5s;
	&:hover{
		background: darken(@maroon, 10%);
	}
}

.cross{
	position: relative;
	padding-right: 20px;
	&::before,
	&::after{
		content: '';
		position: absolute;
		display: inline-block;
		background: #fff;
	}
	&::before{
		top: 4px;
		right: 0;
		width: 11px;
		height: 3px;
	}
	&::after{
		top: 0;
		right: 4px;
		width: 3px;
		height: 11px;
	}

}

.sale{
	position: relative;
	&::before{
		content: 'sale';
		position: absolute;
		top: 0;
		left: 0;
		.inline-block;
		padding-left: 5px;
		padding-top: 5px;
		width: 50px;
		height: 50px;
		font-size: 10px;
		text-transform: uppercase;
		color: #fff;
		background: url("../img/sale.jpg") no-repeat center center;
		@media screen and (max-width: 992px) {
			font-size: 8px;
			width: 40px;
			height: 40px;
		}
	}
}

.shop-it{
	position: relative;
	&::before{
		content:"Shop it";
		position: absolute;
		top: 30%;
		left: 50%;
		transform: translateX(-50%);
		padding-top: 80px;
		width: 135px;
		height: 135px;
		font-size: 12px;
		text-align: center;
		text-transform: uppercase;
		color: #fff;
		background: #4c4c4c url("../img/shop-it-bag.png") no-repeat center 40%;
		border-radius: 50%;
		@media screen and (max-width: 992px) {
			font-size: 10px;
			padding-top: 70px;
			width: 115px;
			height: 115px;
		}
	}
}