.favorite{
	&__title{
		position: relative;
		margin-bottom: 20px;
		text-align: center;
		&::before,
		&::after{
			content: '';
			position: absolute;
			top: 50%;
			width: 37%;
			height: 1px;
			background: black;
			@media screen and (max-width: 992px) {
				width: 34%;
			}
			@media screen and (max-width: 700px) {
					width: 30%;
			}
		}
		@media screen and (max-width: 992px) {
				display: none;
		}
		&::before{
			left: 0;
		}
		&::after{
			right: 0;
		}
	}
	&__content{
		padding-bottom: 20px;
		border-bottom: 1px solid black;
		@media screen and (max-width: 768px) {
			display: none;
		}
	}
	&__item{
		.inline-block;
		width: 100px;
		margin-right: 48px;
		&:last-child{
			margin-right: 0;
		}
		@media screen and (max-width: 1200px) {
			margin-right: 15px;
		}
		&.close{
			@media screen and (max-width: 992px) {
				display: none;
			}
		}
	}
}