.footer-bottom{
	padding: 40px 0;
	color: #8d8d8d;
	background: #000;
	.container{
		position: relative;
	}
	&__list{
		@media screen and (max-width: 768px) {
			display: none;
			padding: 20px;
		}
	}
	&__item{
		.inline-block;
		padding: 0 10px;
		border-left: 1px solid #8d8d8d;
		&:first-child{
			border-left: none;
		}
		@media screen and (max-width: 768px) {
			display: block;
			margin-bottom: 40px;
			border-left: none;
		}
	}
	&__link{
		font-size: 11px;
		color: #8d8d8d;
		&:hover{
			color: #8d8d8d;
			text-decoration: underline;
		}
	}
	&__copy{
		float: right;
	}
}
.show-footer{
	position: relative;
	top: 5px;
	display: none;
	cursor: pointer;
	.close-footer{
		float: left;
		.line{
			margin-bottom: 6px;
			width: 33px;
			height: 6px;
			background: #fff;
		}
	}
	@media screen and (max-width: 768px) {
		display: inline-block;
	}

}