.footer-top{
	padding: 35px 0;
	background: #ececec;
	@media screen and (max-width: 768px) {
		display: none;
	}
	&__title{
		margin-bottom: 20px;
		font-size: 16px;
		font-weight: bold;
		color: #544f4d;
	}
	&__link{
		font-size: 12px;
		line-height: 18px;
		color: #544f4d;
		opacity: .7;
		&:hover{
			text-decoration: underline;
			color: #544f4d;
		}
	}
	.social__item{
		.inline-block;
		margin-right: 6px;
		margin-bottom: 10px;
		width: 35px;
		.line-height(35px);
		text-align: center;
		background: #4d4d4d;
		border-radius: 50%;
		i{
			color: #fff;
		}
	}
}