
.bottom-slider{
	margin-bottom: 60px;
	&__item{
		position: relative;
		height: 400px;
	}
	&__wrapper{
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		.inline-block;
		width: 220px;
		height: 290px;
	}
	.tabs__img{
		width: 220px;
		height: 290px;
		overflow: hidden;
	}
	.tabs__price{
		padding: 10px 0;
			text-align: center;
			text-transform: uppercase;
			background: #f7f7f7;
			&-title{
				font-size: 12px;
			}
			&-content{
				font-size: 22px;
			}
			&-button{
				color: #fff;
			}
			.btn{
				padding: 0;
				padding-left: 10px;
				width: 120px;
				text-align: left;
			}
		}

}
